body {
	background-color: black;
}

.timer {
	align-items: center;
	display: flex;
	justify-content: space-between;
	font-size: 2rem;
	color: #edf3fe;
	
	padding: 0 10px 0 10px;
}

.buttons {
	text-align: center;
	margin-top: 2rem;
}


.lap_result {
	border: 5px solid;
	border-color: #5163c2;
	padding: 1rem;
	margin-top: 1rem;
	color: #edf3fe;
	width: 220px;
}

.lap_title {
	text-align: center;
	font-size: 2rem;
	margin-bottom: 10px;
}